
import Blur from 'vux/src/components/blur/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XProgress from 'vux/src/components/x-progress/index.vue'
import Range from 'vux/src/components/range/index.vue'
import Box from 'vux/src/components/box/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

const prefixInteger = function (num, n) {
return (new Array(n).join(0) + num).slice(-n)
}
export default {
directives: {
TransferDom
},
components: {
Blur,
Flexbox,
FlexboxItem,
XProgress,
Box,
Actionsheet,
Range
},
beforeCreate () {
this.$nextTick(function () {
})
},
created () {

},
beforeMount () {

},
mounted () {

this.createMenu()
this.$nextTick(() => {
this.url = this.playList[this.index].coverUrl
})
},
beforeDestroy () {

},
destroyed () {

},
computed: {
},
methods: {
updateTime (e) {
this.isStop = e.target.ended
if (e.target.ended) {
this.play = false
switch (this.playback) {
case 0:
setTimeout(() => {
this.nextMusic()
}, 100)
break
case 1:
setTimeout(() => {
this.loopMusic()
}, 100)
break
case 2:
setTimeout(() => {
this.randomMusic()
}, 100)
break
}
}
this.player = e.target.currentTime / document.getElementById('music').duration * 100
this.currentTime = prefixInteger(parseInt(e.target.currentTime / 60), 2) + ':' + prefixInteger(parseInt(e.target.currentTime % 60), 2)
this.duration = prefixInteger(parseInt(e.target.duration / 60), 2) + ':' + prefixInteger(parseInt(e.target.duration % 60), 2)
},
change () {
document.getElementById('music').currentTime = this.player * document.getElementById('music').duration / 100
},
circulation () {
this.$vux.toast.show({
type: 'text',
position: 'center',
text: this.playback !== 2 ? this.cir[this.playback + 1] : this.cir[0]
})
if (this.playback < this.cir.length - 1) {
this.playback++
} else {
this.playback = 0
}
},
collect (con) {
this.$vux.toast.show({
type: 'text',
position: 'center',
text: con
})
},
togglePlay () {
const music = document.getElementById('music')
if (!this.play) {
music.play()
this.play = !this.play
} else {
music.pause()
this.play = !this.play
}
},
nextMusic () {
const music = document.getElementById('music')
if (this.index < this.playList.length - 1) {
this.updateTitle(this.playList[this.index + 1].name)
music.src = this.playList[this.index + 1].src
music.play()
this.url = this.playList[this.index + 1].coverUrl
this.play = true
this.index ++
} else {
this.index = 0
this.updateTitle(this.playList[this.index].name)
music.src = this.playList[this.index].src
music.play()
this.url = this.playList[this.index].coverUrl
this.play = true
}
},
lastMusic () {
const music = document.getElementById('music')
if (this.index > 0) {
this.updateTitle(this.playList[this.index - 1].name)
music.src = this.playList[this.index - 1].src
music.play()
this.url = this.playList[this.index - 1].coverUrl
this.play = true
this.index --
} else {
this.index = this.playList.length - 1
this.updateTitle(this.playList[this.index].name)
music.src = this.playList[this.index].src
music.play()
this.url = this.playList[this.index].coverUrl
this.play = true
}
},
loopMusic () {
const music = document.getElementById('music')
this.updateTitle(this.playList[this.index].name)
music.src = this.playList[this.index].src
music.play()
this.play = true
},
randomMusic () {
const music = document.getElementById('music')
const randomIndex = Math.floor(Math.random() * this.playList.length)
this.updateTitle(this.playList[randomIndex].name)
music.src = this.playList[randomIndex].src
music.play()
this.url = this.playList[randomIndex].coverUrl
this.play = true
},
createMenu () {
this.menus.push({
label: '播放列表',
type: 'info'
})
for (let i in this.playList) {
this.menus.push({
label: '<div style="text-align: left;padding: 0 1rem;"><span style="font-size: 1.1rem">' + this.playList[i].name + '</span></div>',
value: this.playList[i].id
})
}
},
playMenu (key, item) {
const music = document.getElementById('music')
this.updateTitle(this.playList[key - 1].name)
music.src = this.playList[key - 1].src
music.play()
this.url = this.playList[key - 1].coverUrl
this.play = true
},
updateTitle (name) {
this.$store.dispatch('updateTitle', name)
}
},
data () {
return {
playback: 0,
cir: ['列表循环', '单曲循环', '随机播放'],
url: '',
showHideOnBlur: false,
player: 0,
musicUrl: this.$route.params.src,
index: this.$route.params.index,
musicTwoUrl: './static/music/music-2.mp3',
discUrl: './static/images/needle-plus-2.png',
discTopUrl: './static/images/needle-top.png',
play: false,
duration: '--:--',
currentTime: '00:00',
playList: this.$route.params.playlist,
songMenu: false,
singer: false,
hide: '<span></span>',
singerInfo: {
'language.noop': '<span class="menu-title">快捷菜单</span>',
'zh-CN': '歌手：徐秉龙',
'en': '专辑： 迂回',
'third': '收藏到歌单'
},
menus: [






]
}
}
}



